
.details {
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 1em;
  border: none;
}
.light {
  background: #F5F5F5;
  color: #212121;
}
.dark {
  background: #000000;
  color: #EEE;
}



