
.menu-bar {
  width: 100%;
  height: auto;
  padding: 1em 0 1em 0;
  position: fixed;
  display: grid;
  grid-template-columns: 3.5em auto 3.5em;
}
.menu-bar .router {
    color: black;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
}
.menu-bar .btn {
    width: 1.5em;
    height: 1.5em;
    margin: 0;
    padding: 0;
    border: none;
}
.menu-bar .description {
    justify-self: center;
    align-self: center;
    margin: 0;
    padding: 0;
}
.light {
  background: #F5F5F5;
  color: #212121;
  border-bottom: solid 1px #BDBDBD;
}
.light .hamburger {
    background: url("./assets/hamburger-light.svg");
    background-size: contain;
    background-repeat: no-repeat;
}
.dark {
  background: #000000;
  color: #EEE;
  border-bottom: solid 1px #424242;
}
.dark .hamburger {
    background: url("./assets/hamburger-dark.svg");
    background-size: contain;
    background-repeat: no-repeat;
}



